<template>
  <v-container pa-1>
    <v-layout column>
      <v-progress-circular
        :rotate="-90"
        :size="100"
        :width="15"
        :value="percentComplete"
        color="primary"
        >sent {{ count }} {{ percentComplete }}%</v-progress-circular
      >
      <v-layout justify-center ma-4>
        <v-chip color text-color="black">
          <v-avatar>
            <v-icon>account_circle</v-icon>
          </v-avatar>
          last sent: {{ email }}
        </v-chip>
      </v-layout>
      <span v-html="compiledHtmlMessage"></span>

      <p>{{ delay }} secs</p>
      <v-form v-model="valid" v-on:submit.prevent="onSubmit">
        <v-flex xs12 sm6 d-flex>
          <v-select
            :items="delays"
            v-model="delay"
            box
            label="Send Interval(sec)"
          ></v-select>
        </v-flex>
        <v-switch
          v-model="autoSMTP"
          :label="`Use auto-smtp: ${autoSMTP.toString()}`"
        ></v-switch>
        <v-flex xs12 sm6 d-flex>
          <v-select
            :disabled="autoSMTP == false"
            :items="providers"
            v-model="provider"
            box
            label="Email Provider"
          ></v-select>
        </v-flex>
        <v-text-field
          v-model="smtpHost"
          :disabled="autoSMTP"
          box
          label="SMTP Host"
          type="text"
          required
        ></v-text-field>

        <v-textarea
          v-model="providerUserPass"
          box
          label="SMTP(user:password) one per line"
          required
        ></v-textarea>

        <v-switch
          v-model="useProxy"
          :label="`Use Proxy: ${useProxy.toString()}`"
        ></v-switch>
        <v-text-field
          v-if="useProxy"
          v-model="proxy"
          box
          label="Proxy"
          type="text"
          required
        ></v-text-field
        >%company_name% %company_domain% %random_string% %date%
        <v-text-field
          v-model="senderName"
          box
          label="Sender's Name (defaults to %company_name% if empty)"
          type="text"
          required
        ></v-text-field
        >%company_name% %company_domain% %random_string%
        <v-text-field
          v-model="senderEmail"
          box
          label="Sender's Email(Required for AWS or sendgrid)"
          type="text"
          required
        ></v-text-field
        >%name% %email% %encoded_email% %company_name%, %company_domain%,
        %random_string%
        <v-textarea
          v-model="pageURLs"
          box
          label="pageUrls"
          required
        ></v-textarea>
        <v-textarea
          v-model="emailName"
          box
          label="Recipients(id@domain.com,Name) one per line"
          required
        ></v-textarea
        >%name% %company_name%, %company_domain%, %random_string%
        <v-text-field
          v-model="subject"
          box
          label="Subject"
          type="text"
          required
        ></v-text-field
        >%page_url% %name% %email% %encoded_email% %company_name%
        %company_domain% %random_string% %date%
        <v-textarea
          v-model="htmlMessage"
          box
          label="HTML Message"
          required
        ></v-textarea>
      </v-form>
      <v-btn v-on:click="submitForm" small color="blue" dark>SEND</v-btn>
    </v-layout>
  </v-container>
</template>

<script>
import * as htmlToText from 'html-to-text';
import { fromString } from 'uuidv4';
export default {
  data() {
    return {
      useProxy: false,
      proxy: 'user:pass@ip:port',
      pdfSource: '',
      attachmentOption: 'no-attachment',
      upLoadedFile: null,
      uploadedFileName: '',
      uploadedFileSize: null,
      percentageUploaded: 0,
      snackbar: false,
      snackbarText: '',
      providers: [
        'Naver',
        'Gmail',
        'SES-EU-WEST-1',
        'SendGrid',
        'Mailgun',
        'Mailjet',
        'Mandrill',
        'SendPulse',
        'SendinBlue',
        'PostmarkApp',
        'Sparkpost',
        'Godaddy',
        'GodaddyAsia',
        'GodaddyEurope',
        'Tipimail',
      ],
      provider: 'SendGrid',
      smtpHost: 'host:port',
      autoSMTP: true,
      delays: [
        0,
        0.1,
        0.2,
        0.3,
        0.5,
        1,
        2,
        3,
        4,
        5,
        7,
        10,
        13,
        15,
        17,
        20,
        3600,
      ],
      delay: 0,
      count: 0,
      value: 0,
      valid: false,
      emailName: '',
      name: '',
      email: '',
      senderName: '',
      senderEmail: '',
      providerUserPass: '',
      providerUser: '',
      providerPass: '',
      subject: '',
      companyDomain: '',
      companyName: '',
      htmlMessage: ``,
      pageURLs: '',
      pageURL: '',
      sendDisabled: false,
      percentComplete: 0,
    };
  },

  computed: {
    emailSize: function() {
      return this.emailName.split('\n').length;
    },

    encodedEmail() {
      return window.btoa(this.email);
    },

    randomString() {
      return fromString(this.email);
    },

    compiledSenderEmail() {
      return this.senderEmail
        .split('%company_name%')
        .join(this.companyName)
        .split('%company_domain%')
        .join(this.companyDomain)
        .split('%random_string%')
        .join(this.randomString);
    },

    compiledSenderName() {
      return this.senderName
        .split('%company_name%')
        .join(this.companyName)
        .split('%company_domain%')
        .join(this.companyDomain)
        .split('%date%')
        .join(new Date().toUTCString())
        .split('%random_string%')
        .join(this.randomString);
    },

    compiledSubject() {
      return this.subject
        .split('%name%')
        .join(this.name)
        .split('%email%')
        .join(this.email)
        .split('%company_name%')
        .join(this.companyName)
        .split('%company_domain%')
        .join(this.companyDomain)
        .split('%date%')
        .join(new Date().toUTCString())
        .split('%random_string%')
        .join(this.randomString);
    },

    compiledHtmlMessage: function() {
      return this.htmlMessage
        .split('%page_url%')
        .join(this.pageURL)
        .split('%name%')
        .join(this.name)
        .split('%email%')
        .join(this.email)
        .split('%encoded_email%')
        .join(this.encodedEmail)
        .split('%company_name%')
        .join(this.companyName)
        .split('%company_domain%')
        .join(this.companyDomain)
        .split('%date%')
        .join(new Date().toUTCString())
        .split('%random_string%')
        .join(this.randomString);
    },
  },

  created() {},

  methods: {
    async submitForm() {
      function sleep(ms) {
        return new Promise((resolve) => setTimeout(resolve, ms));
      }

      let emailNamelist = this.emailName.split('\n');
      let pageURLsList = this.pageURLs.split('\n');

      let providerUserPassList = this.providerUserPass.split('\n');
      console.log(this.delay);

      console.log(emailNamelist);
      console.log(emailNamelist.length);

      /**
       * loop though emails provided `email,name`
       */
      for (let i = 0; i < emailNamelist.length; i++) {
        // go round list of emails
        let provider = this.$rr(providerUserPassList);

        // go round list of page links.
        let pageURL = this.$rr(pageURLsList);

        let providerUser = provider.split(':')[0].trim();
        let providerPass = provider.split(':')[1].trim();

        this.email = emailNamelist[i].split(',')[0];
        this.count = i + 1;
        console.log(this.count);
        this.percentComplete = Math.ceil(
          (this.count / emailNamelist.length) * 100
        );
        console.log(this.email);
        this.providerUser = providerUser;
        this.providerPass = providerPass;
        this.pageURL = pageURL;
        console.log(providerUser, providerPass, pageURL);

        this.name = emailNamelist[i].split(',')[1];
        this.companyDomain = this.email.split('@')[1];
        let companyName = this.email.split('@')[1].split('.')[0];
        this.companyName =
          companyName.charAt(0).toUpperCase() + companyName.slice(1);

        let postData = {
          senderName: this.compiledSenderName,
          senderEmail: this.compiledSenderEmail,
          email: this.email,
          htmlMessage: this.$spintax.unspin(this.compiledHtmlMessage),
          textMessage: htmlToText.fromString(
            this.$spintax.unspin(this.compiledHtmlMessage)
          ),
          subject: this.$spintax.unspin(this.compiledSubject),
          provider: this.provider,
          providerUser,
          providerPass,
          proxy: this.proxy,
          useProxy: this.useProxy,
          smtpHost: this.smtpHost,
          autoSMTP: this.autoSMTP,
        };
        let postResponse = await this.$http.post(
          'https://74g2hk33tg.execute-api.us-east-1.amazonaws.com/dev/mail/send',
          JSON.stringify(postData)
        );
        console.log(postResponse.data);
        await sleep(this.delay * 1000);
      }
    },
  },
};

// try {
//   await this.$firestore.users.add({
//     senderName: this.compiledSenderName,
//     senderEmail: this.compiledSenderEmail,
//     email: this.email,
//     htmlMessage: this.$spintax.unspin(this.compiledHtmlMessage),
//     textMessage: htmlToText.fromString(
//       this.$spintax.unspin(this.compiledHtmlMessage)
//     ),
//     subject: this.$spintax.unspin(this.compiledSubject),
//     provider: this.provider,
//     providerUser,
//     providerPass,
//     proxy: this.proxy,
//     useProxy: this.useProxy,
//     smtpHost: this.smtpHost,
//     autoSMTP: this.autoSMTP,
//   });
// } catch (e) {
